<script>
import Vue from 'vue'
import { TradeData, currency, TradeMoney, TradeMoneyRepay, TradeBusiness, TradePry } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
import { DropdownMenu, DropdownItem, Icon, Overlay, Popup, Field, Toast } from 'vant'
Vue.use(DropdownMenu).use(DropdownItem).use(Icon).use(Overlay).use(Popup).use(Field).use(Toast)

export default {
  components: {},
  data () {
    return {
      u_id: '',
      amounum: '', // 最多不能卖出的数量
      num: '', // 借币数量
      show_alert: false,
      show_alert2: false,
      value1: 0,
      value2: 'a',
      mask_bg_isShow: false,
      mask1_bg_isShow: false,
      kk2_isShow: false,
      option1: [
        { text: 'BTC/USDT', value: 0 },
        { text: 'ETH/USDT', value: 1 },
        { text: 'LBTH/USDT', value: 2 }
      ],
      parameter: {
        u_id: '10'
      },
      currencyData: {
        u_id: '10',
        number: '',
        rate: ''
      },
      TradeMoneyData: {
        u_id: '10'
      },
      TradeMoneyRepayData: {
        u_id: '10',
        number: ''
      },
      add0 (m) { return m < 10 ? '0' + m : m },
      format (isTime) {
        var times = isTime * 1000
        var time = new Date(times)
        var y = time.getFullYear()
        var m = time.getMonth() + 1
        var d = time.getDate()
        // var h = time.getHours()
        // var mm = time.getMinutes()
        // var s = time.getSeconds()
        // console.log(y + '-' + this.add0(m) + '-' + this.add0(d) + ' ' + this.add0(h) + ':' + this.add0(mm) + ':' + this.add0(s))
        return y + '-' + this.add0(m) + '-' + this.add0(d)
      },
      TradeBusinessData: {
        u_id: '10',
        price: '', // 当前行情价
        number: '', // 购买数量
        cls: 'BTC', // 币种类型
        type: '' // 类型：1买入，2卖出
      }, // 杠杆买卖参数对象
      TradePryData: {
        u_id: '10',
        market: 'btc_usdt'
      }, // 杠杆多列表参数传参
      TradePryList: [],
      isAmount: '9.125',
      iptText: '1',
      TradeMoneyList: [],
      TradeList: [],
      userBalancel: '998', // 可用余额
      isSelect: 0,
      spanActive: 0
    }
  },
  created () {
    this.u_id = sessionStorage.getItem('u_id')
    this.parameter.u_id = sessionStorage.getItem('u_id')
    this.TradeMoneyData.u_id = sessionStorage.getItem('u_id')
    this.TradeMoneyRepayData.u_id = sessionStorage.getItem('u_id')
    this.TradeBusinessData.u_id = sessionStorage.getItem('u_id')
    this.TradePryData.u_id = sessionStorage.getItem('u_id')
    // var data = objKeySort(this.parameter)
    // var token = sgin(data)
    // this.parameter.token = token
    // this.TradeFun() // 初始化借币参数
    // this.TradeMoneyFun() // 初始化归还参数
    this.TradePryFun() // 主要列表数据
  },
  methods: {
    toOrder () {
      this.$router.push({
        path: '/personal/order',
        query: {
          types: '杠杆'
        }
      })
    },
    navClick (type) {
      this.spanActive = type
    },
    userSelect (type) {
      this.isSelect = type
    },
    // 杠杆主要内容接口
    TradePryFun () {
      var that = this
      var data = objKeySort(that.TradePryData)
      var token = sgin(data)
      that.TradePryData.token = token
      TradePry(that.TradePryData).then(res => {
        console.log('杠杆列表数据', res)
        if (res.code === 0) {
          that.TradePryList = res.data
          that.amounum = res.data.list.amounum
          this.userBalancel = res.data.list.usdt
          delete that.TradePryData.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.TradePryData.token
      })
    },
    // 杠杆买卖
    TradeBusinessFun (type) {
      console.log(type, 'type')
      var that = this
      if (type === 2) {
        var text = '卖出数量不能大于' + that.amounum
        if (that.amounum < that.iptText) {
          that.$toast({ message: text, duration: 1500 })
          return
        }
      }
      if (that.iptText === '') {
        that.$toast({ message: '数量不能为空', duration: 1500 })
        return
      }
      that.TradeBusinessData.type = type
      that.TradeBusinessData.number = that.iptText
      that.TradeBusinessData.price = that.isAmount
      var data = objKeySort(that.TradeBusinessData)
      var token = sgin(data)
      that.TradeBusinessData.token = token
      // console.log(that.TradeBusinessData, 'that.TradeBusinessData')
      TradeBusiness(that.TradeBusinessData).then(res => {
        console.log('杠杆买卖', res)
        if (res.code === 0) {
          that.$toast({ message: '成功', duration: 1500 })
          // that.TradeBusinessData = res.data
          that.iptText = ''
          delete that.TradeBusinessData.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.TradeBusinessData.token
      })
    },
    // 借币全部
    numWhole () {
      this.num = this.TradeList.balance
    },
    // 点击归还
    TradeMoneyRepayFun () {
      var that = this
      that.TradeMoneyRepayData.number = that.TradeMoneyList.z_amount
      var data = objKeySort(that.TradeMoneyRepayData)
      var token = sgin(data)
      this.TradeMoneyRepayData.token = token
      TradeMoneyRepay(that.TradeMoneyRepayData).then(res => {
        if (res.code === 0) {
          that.$toast({ message: '归还成功', duration: 1500 })
          that.num = ''
          that.show_alert2 = false
          delete that.currencyData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.num = ''
        delete that.currencyData.token
      })
    },
    // 归还列表查询
    TradeMoneyFun () {
      var that = this
      var data = objKeySort(that.TradeMoneyData)
      var token = sgin(data)
      that.TradeMoneyData.token = token
      console.log(that.TradeMoneyData, 'that.TradeMoneyData')
      TradeMoney(that.TradeMoneyData).then(res => {
        console.log('杠杆归还', res)
        if (res.code === 0) {
          that.TradeMoneyList = res.data
          delete that.TradeMoneyData.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.TradeMoneyData.token
      })
    },
    // 借币列表查询
    TradeFun () {
      var that = this
      var data = objKeySort(this.parameter)
      var token = sgin(data)
      this.parameter.token = token
      TradeData(that.parameter).then(res => {
        console.log('杠杆借币', res)
        if (res.code === 0) {
          that.TradeList = res.data
          delete that.parameter.token
        }
      }).catch((err) => {
        console.log(err)
        delete that.parameter.token
      })
    },
    // 点击借币
    currencyFun () {
      var that = this
      console.log(that.num, 'that.num')
      if (that.num === '') {
        that.$toast({ message: '借币数量不能为空', duration: 1500 })
        return
      }
      if (Number(that.num) < 100) {
        that.$toast({ message: '最少借100U', duration: 1500 })
        return
      }
      that.currencyData.number = that.num
      that.currencyData.rate = that.TradeList.rate
      var data = objKeySort(this.currencyData)
      var token = sgin(data)
      this.currencyData.token = token
      currency(that.currencyData).then(res => {
        if (res.code === 0) {
          that.$toast({ message: '借币成功', duration: 1500 })
          that.num = ''
          that.show_alert = false
          delete that.currencyData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        that.num = ''
        delete that.currencyData.token
      })
    },
    // 弹出框借币
    change_show_alert1 () {
      this.show_alert = !this.show_alert
      console.log('ccc')
      if (this.show_alert) {
        this.TradeFun()
      }
    },
    // 弹出框归还
    change_show_alert2 () {
      console.log('ccc')
      this.show_alert2 = !this.show_alert2
      if (this.show_alert2) {
        this.TradeMoneyFun()
      }
    },
    showPopup () {
      this.show = true
    },
    //   弹出借币 弹出框
    get_jiebi_kk () {
      this.show = !this.show
    },
    //   弹出借币 弹出框
    get_guihuan_kk () {
      this.mask1_bg_isShow = !this.mask1_bg_isShow
    },
    // 关闭 框框
    closeKK () {
      console.log('gggg')
      this.mask_bg_isShow = !this.mask_bg_isShow
    },
    // 关闭 框框
    closeKK1 () {
      console.log('gggg')
      this.mask1_bg_isShow = !this.mask1_bg_isShow
    }
  }
}
</script>
